@import "../../../styles/blocks/vars.scss";
@import "../../../styles/blocks/mixins.scss";

.new {
    padding-top: 184px;

    @include onTablet {
        padding-top: 140px;
    }

    @include onTablet {
        padding-top: 120px;
    }


    &__content {
        &-title {
            padding: 30px 0;
            padding-bottom: 15px;
            font-family: "Catamaran";
            font-style: normal;
            font-weight: bold;
            font-size: 34px;
            line-height: 39px;
            color: $text-color-one;
        }

        &-text {
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 400;
            font-size: 19px;
            line-height: 30px;
            color: $blog-txt;

            &>a {
                text-decoration: underline;
            }
        }

        &-strong {
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 400;
            font-size: 19px;
            line-height: 30px;
            color: $text-color-one;
            margin-top: 42px;
        }
    }
}