@import "../blocks/vars.scss";
@import "../blocks/mixins.scss";

.examples {
  max-width: 1064px;
  margin: 250px auto;
  padding: 0 20px;

  @include onTablet {
    max-width: 764px;
    margin: 169px auto;
    animation: none;
  }

  @include onMobile {
    max-width: 764px;
    margin-top: 170px;
    margin-bottom: 130px;
  }
}

.texting {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 75px;

  @include onTablet {
    gap: 18px;
  }

  @include onMobile {
    flex-direction: column;
    align-items: flex-start;
    gap: 14px;
    margin-bottom: 30px;
  }

  &__title {
    font-family: "Catamaran";
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    line-height: 115%;
    text-align: left;
    color: $text-color-one;
    flex-grow: 1;
    max-width: 400px;

    @include onTablet {
      font-size: 36px;
      line-height: 43px;
      max-width: 298px;
    }

    @include onMobile {
      font-size: 30px;
      line-height: 36px;
    }
  }

  &__text {
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: left;
    color: $text-color-two;
    max-width: 488px;
    flex-grow: 1;

    @include onTablet {
      font-size: 18px;
      max-width: 400px;
    }
  }
}

.row__up {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;

  @include onTablet {
    margin-bottom: 23px;
  }

  @include onMobile {
    flex-direction: column;
    margin-bottom: 20px;
  }

  &-small {
    position: relative;
    border-radius: 10px;
    width: 305px;
    overflow: hidden;
    flex-grow: 1;
    cursor: url("../../assets/photos/examples/hover.png") 80 80, auto;

    @include onMobile {
      width: 100%;
      max-height: 400px;
    }

    & > img {
      object-fit: cover;
      width: 100%;
      border-radius: 10px;
      transition: 0.7s;

      &:hover {
        transform: scale(1.1);
      }
    }

    & > p {
      position: absolute;
      bottom: 40px;
      left: 50%;
      width: 100%;
      transform: translate(-50%, 0);
      font-style: normal;
      font-weight: 800;
      font-size: 30px;
      line-height: 120%;
      text-align: center;
      color: $default-white;
      font-family: "Catamaran";

      @include onTablet {
        font-size: 22px;
      }

      @include onMobile {
        font-size: 24px;
        bottom: 27px;
      }
    }
  }

  &-big {
    width: 738px;
    flex-grow: 1;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    cursor: url("../../assets/photos/examples/hover.png") 80 80, auto;

    @include onTablet {
      max-width: 530px;
    }

    @include onMobile {
      max-width: 100%;
      width: 100%;
      max-height: 400px;
      height: 400px;
    }

    & > img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      transition: 0.7s;

      &:hover {
        transform: scale(1.1);
      }
    }

    & > p {
      font-family: "Catamaran";
      position: absolute;
      bottom: 40px;
      left: 50%;
      text-align: right;
      font-style: normal;
      font-weight: 800;
      line-height: 120%;
      font-size: 30px;
      color: $default-white;
      transform: translate(-50%, 0px);

      @include onTablet {
        font-size: 22px;
      }

      @include onMobile {
        font-size: 24px;
        bottom: 27px;
      }
    }
  }
}

.row__bottom {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;

  @include onMobile {
    flex-direction: column;
  }

  &-box {
    cursor: url("../../assets/photos/examples/hover.png") 80 80, auto;
  }
}

.box {
  width: 733px;
  height: 508px;
  flex-grow: 1;
  border-radius: 10px;
  overflow: hidden;
  position: relative;

  @include onTablet {
    max-width: 528px;
    height: 366px;
  }

  @include onMobile {
    max-width: 100%;
    width: 100%;
    max-height: 400px;
    height: 400px;
  }

  & > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
    transition: 0.7s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__title {
    font-family: "Catamaran";
    position: absolute;
    bottom: 40px;
    left: 20%;
    text-align: left;
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 120%;
    color: $text-color-one;
    transform: translate(-50%, 0px);

    @include onTablet {
      font-size: 22px;
    }

    @include onMobile {
      font-size: 24px;
      position: absolute;
      bottom: 27px;
    }
  }
}

.back {
  &-mobile {
    display: none;

    @include onTablet {
      display: block;
    }
  }

  &-desktop {
    @include onTablet {
      display: none;
    }
  }
}

.more {
  position: relative;
  border: none;
  border-radius: 10px;
  width: 306px;
  overflow: hidden;
  flex-grow: 1;
  background-color: $example-more;
  transition: background-color 1.2s;
  height: 508px;
  padding: 0;

  @include onTablet {
    max-width: 220px;
    height: 366px;
  }

  @include onMobile {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: none;
    height: 44px;
    width: 220px;
    background: $default-white;
    border: 2px solid $main-click;
    border-radius: 43px;
    margin-top: 23px;
  }

  &:hover {
    background-color: $benefits-bckg;

    & > .changing {
      color: $default-white;

      &::after {
        content: url("../../assets/photos/examples/arrow-white.svg");
        position: absolute;
        right: -45px;
        top: 1px;

        @include onTablet {
          transform: scale(0.6);
          top: -1px;
          right: -35px;
        }

        @include onMobile {
          display: none;
        }
      }
    }
  }

  &__title {
    font-family: "Catamaran";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    color: $text-color-one;
    position: absolute;
    left: 45%;
    top: 50%;
    transform: translate(-50%, 0);
    cursor: pointer;

    @include onTablet {
      font-size: 14px;
    }

    @include onMobile {
      position: static;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      transform: none;
    }

    &::after {
      content: url("../../assets/photos/examples/arrow.svg");
      position: absolute;
      right: -40px;
      top: 1px;

      @include onTablet {
        transform: scale(0.6);
        top: -1px;
        right: -35px;
      }

      @include onMobile {
        display: none;
      }
    }
  }
}
