@import "../../../styles/blocks/vars.scss";
@import "../../../styles/blocks/mixins.scss";

.career {
    padding-top: 178px;

    @include onMobile {
        padding-top: 140px;
    }

    &__container {
        &-build {
            margin-top: 195px;
        }
    }
}

.entry {
    max-width: 1064px;
    margin: 0 auto;
    display: flex;
    padding: 0 20px;
    animation: fadeIn 2s;

    @include onTablet {
        max-width: 764px;
    }

    @include onMobile {
        flex-direction: column;
    }

    &__desc {
        position: relative;
        margin-top: 30px;

        @include onMobile {
            margin-top: 80px;
        }

        &-title {
            font-family: 'Catamaran';
            font-style: normal;
            max-width: 483px;
            font-weight: 800;
            font-size: 44px;
            line-height: 54px;
            color: $text-color-one;

            @include onTablet {
                font-size: 36px;
                line-height: 43px;
            }
        }

        &-text {
            font-family: 'SF Pro Display';
            font-style: normal;
            font-weight: 400;
            font-size: 22px;
            line-height: 33px;
            max-width: 412px;
            color: $blog-txt;
            margin-top: 23px;
            margin-bottom: 60px;

            @include onTablet {
                font-size: 18px;
                line-height: 27px;
            }
        }

        &-link {
            padding: 13px 32px;
            color: $default-white;
            background: $main-click;
            border-radius: 90px;
            font-family: 'SF Pro Display';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            letter-spacing: 0.02em;
        }
    }
}

.imagebox {
    position: relative;
    margin-left: 7vw;

    &-mobile {
        display: none;

        @include onMobile {
            display: block;
        }
    }

    &-desktop {
        @include onMobile {
            display: none;
        }
    }

    &__poster {
        max-width: 543px;

        &-img {
            width: 100%;
        }
    }

    &__list {
        &-second {
            position: absolute;
            right: 1%;
        }
    }
}

.conditions {
    margin: 0 auto;
    margin-top: 70px;
    max-width: 1064px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include onTablet {
        max-width: 764px;
    }
}

.species {
    display: flex;
    padding: 34px 33px;
    background: $default-white;
    max-width: 500px;
    width: 100%;
    box-shadow: 0px 24px 60px rgba(53, 52, 116, 0.08);
    border-radius: 10px;
    margin-bottom: 24px;

    @media (max-width: 1054px) {
        max-width: 485px;
    }

    @include onTablet {
        padding: 20px;
        max-width: 355px;
    }

    @include onMobile {
        flex-direction: column;
        max-width: none;
    }

    &__image {
        background: #F8F9FD;
        border: 1px solid #EFEFF0;
        width: 90px;
        height: 90px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 90px;
        margin-right: 18px;
    }

    &__text {

        @include onMobile {
            margin-top: 20px;
        }

        &-heading {
            font-family: 'Catamaran';
            font-style: normal;
            font-weight: 800;
            font-size: 24px;
            line-height: 36px;
            color: $text-color-one;
        }

        &-small {
            font-family: 'SF Pro Display';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            color: $blog-txt;
        }
    }

}

.openings {
    max-width: 1064px;
    margin: 0 auto;
    padding: 0 20px;
    margin-top: 209px;
    margin-bottom: 253px;
    z-index: 2;

    @include onTablet {
        max-width: 764px;
        margin-top: 150px;
    }

    &__title {
        font-family: 'Catamaran';
        font-style: normal;
        font-weight: 800;
        font-size: 42px;
        line-height: 63px;
        color: $text-color-one;

        @include onTablet {
            font-size: 36px;
            line-height: 43px;
        }
    }

    &__box {
        display: flex;
        gap: 22px;
        margin-top: 70px;

        @include onTablet {
            margin-top: 50px;
        }

        @include onMobile {
            flex-direction: column;
        }
    }
}

.vacancy {
    background: $default-white;
    max-width: 336px;
    box-shadow: 0px 24px 60px rgba(53, 52, 116, 0.08);
    border-radius: 10px;

    @include onMobile {
        max-width: none;
    }

    &__items {
        padding: 32px 32px 48px;

        &-fire {
            max-width: 52px;
            width: 52px;
            height: 52px;
            background: $poster-bckg;
            border: 1px solid $poster-border;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &-big {
            margin-top: 18px;
            font-family: 'Catamaran';
            font-style: normal;
            font-weight: 800;
            font-size: 20px;
            line-height: 30px;
            display: flex;
            align-items: center;
            color: $text-color-one;
        }

        &-small {
            font-family: 'SF Pro Display';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            color: $blog-txt;
        }
    }

    &__more {
        padding: 12px 28px;
        border: 1px solid $text-color-one;
        border-radius: 90px;
        font-family: 'SF Pro Display';
        font-style: normal;
        font-weight: 800;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.02em;
        color: $text-color-one;
    }
}

.det {
    padding-bottom: 44px;
}

.informing {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 40px;
    height: 100%;

    &__details {
        display: flex;
        margin-top: 18px;

        &-text {
            margin-left: 10px;
            font-family: 'SF Pro Display';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 26px;
            color: $text-color-one;
        }
    }
}