@import "../../../styles/blocks/vars.scss";
@import "../../../styles/blocks/mixins.scss";

.services {
    padding-top: 184px;

    @include onTablet {
        padding-top: 140px;
    }

    @include onTablet {
        padding-top: 120px;
    }

}

.details {
    &__txt {
        font-family: "SF Pro Display";
        font-style: normal;
        font-weight: 400;
        font-size: 19px;
        line-height: 30px;
        color: $blog-txt;
    }

    &__gen {
        padding: 68px 0;
        padding-bottom: 15px;
        font-family: "Catamaran";
        font-style: normal;
        font-weight: bold;
        font-size: 34px;
        line-height: 44px;
        color: $text-color-one;
    }

    &__numeration {
        display: flex;
        flex-direction: column;
        gap: 30px;

        &-text {
            font-family: "SF Pro Display";
            font-style: normal;
            font-weight: 400;
            font-size: 19px;
            line-height: 34px;
            color: $blog-txt;

            &>span {
                font-weight: 800;
                font-size: 28px;
                background: -webkit-linear-gradient(rgba(253, 191, 117, 1), rgba(195, 190, 204, 1));
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }

    &__advantages {
        margin-top: 15px;
        margin-bottom: 15px;
        max-width: 702px;

        &-photo {
            width: 100%;
            height: auto;
        }
    }

    &__desc {
        display: flex;
        flex-direction: column;
        gap: 42px;
    }
}

.writn {
    &__title {
        font-family: "Catamaran";
        font-style: normal;
        font-weight: 800;
        font-size: 34px;
        line-height: 30px;
        color: $text-color-one;
    }

    &__txt {
        margin-top: 14px;
        font-family: "SF Pro Display";
        font-style: normal;
        font-weight: 400;
        font-size: 19px;
        line-height: 30px;
        color: $blog-txt;
    }
}