@import "../../../styles/blocks/vars.scss";
@import "../../../styles/blocks/mixins.scss";

.introducion {
  padding-top: 90px;
  margin-bottom: 35vw;
  position: relative;
  margin-top: 50px;

  @include onMobile {
    padding-top: 0;
    margin-top: 60px;
  }

  &__container {
    max-width: 1064px;
    margin: 0 auto;
    display: flex;

    @include onTablet {
      max-width: 764px;
    }

    @include onMobile {
      flex-direction: column;
    }

    &-big {
      z-index: 2;
      position: relative;
      top: 75px;
      animation: fadeIn 2s;

      @include onTablet {
        top: 5px;
        animation: none;
      }

      @include onMobile {
        display: none;
      }

      & > h2 {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 900;
        font-size: 56px;
        color: $default-white;
        text-shadow: 0px 4px 18px rgb(28 33 86 / 17%);
        width: 100%;

        @include onTablet {
          font-size: 41px;
          line-height: 49px;

          @include onMobile {
            font-size: 22px;
            line-height: 26px;
          }
        }
      }
    }

    &-phone {
      position: absolute;
      width: 21vw;
      right: 24%;
      top: 50%;
      z-index: 2;
      animation: fadeIn 2s;

      @include onMobile {
        display: none;
      }

      & > img {
        width: 100%;
      }
    }

    &-letter {
      width: 35vw;
      position: absolute;
      right: 0;
      top: -20px;
      z-index: 1;
      animation: fadeIn 1s;

      @include onTablet {
        top: -20px;
      }

      @include onMobile {
        display: none;
      }

      & > img {
        width: 100%;
      }
    }

    &-mobi {
      display: none;
      width: 90vw;
      max-width: 600px;
      margin: 0 auto;
      animation: fadeIn 2s;

      & > img {
        object-fit: cover;
        width: 100%;
      }

      @include onMobile {
        display: block;
      }
    }
  }

  &__bg {
    position: absolute;
    width: 70%;
    top: -130px;
    z-index: -1;
    right: 0;
    padding-bottom: 22vw;
    animation: fadeIn 2s;

    @include onMobile {
      width: 100%;
      top: -80px;
    }

    &-img {
      width: 100%;
    }
  }
}

.intro {
  padding-top: 110px;
  padding-left: 20px;
  animation: fadeIn 2s;

  @include onTablet {
    padding-top: 10px;
    margin-right: 50px;
  }

  @include onMobile {
    padding-top: 0;
  }

  &__title {
    font-family: "Catamaran";
    font-style: normal;
    font-weight: 800;
    font-size: 44px;
    line-height: 120%;
    max-width: 540px;
    color: $text-color-one;

    @include onTablet {
      font-size: 36px;
      line-height: 43px;
      max-width: 325px;
    }
  }

  &__text {
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 150%;
    padding-top: 25px;
    color: $text-color-two;
    max-width: 490px;
    padding-bottom: 110px;

    @include onTablet {
      font-size: 20px;
      line-height: 31px;
      padding-top: 6px;
      padding-bottom: 78px;
      max-width: 334px;
    }
  }

  &__link {
    padding: 12px 30px;
    background: $main-click;
    border-radius: 90px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    font-family: "SF Pro Display";
    letter-spacing: 0.02em;
    color: $default-white;

    @include onMobile {
      padding: 18px 33px;
    }
  }
}

.building {
  position: relative;

  &__info {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 1064px;
    padding: 0 25px;
    margin-bottom: 15vw;

    &-title {
      max-width: 507px;
      font-family: "Catamaran";
      font-style: normal;
      font-weight: 800;
      font-size: 48px;
      line-height: 120%;
      color: $text-color-one;
      flex-grow: 1;
    }

    &-text {
      max-width: 480px;
      flex-grow: 1;

      & > p {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        font-family: "SF Pro Display";
        color: $text-color-two;
        padding-bottom: 25px;
      }
    }
  }

  &__bg {
    position: absolute;
    left: 0;
    top: -42px;
    width: 57vw;
    z-index: -1;
    max-width: 580px;

    &-img {
      width: 100%;
    }
  }
}

.presentation {
  margin-top: 220px;

  &__title {
    font-family: "Catamaran";
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    color: $text-color-one;
    margin: 0 auto;
    text-align: center;

    @include onTablet {
      font-size: 36px;
      line-height: 43px;
    }

    @include onMobile {
      font-size: 30px;
      line-height: 36px;
      max-width: 344px;
    }
  }

  &__desc {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    font-family: "SF Pro Display";
    padding-top: 20px;
    margin: 0 auto;
    max-width: 570px;
    text-align: center;
    color: $text-color-two;

    @include onTablet {
      font-size: 18px;
      line-height: 27px;
      max-width: 506px;
    }
  }

  &__container {
    margin-top: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 23px;
    padding: 0 20px;

    @include onMobile {
      flex-direction: column;
      margin-top: 42px;
    }
  }
}

.poster {
  transition: 0.6s;
  padding: 28px;
  width: 338px;
  height: 373px;
  background: $default-white;
  box-shadow: 0px 24px 60px rgb(53 52 116 / 16%);
  border-radius: 10px;

  @include onTablet {
    max-width: 244px;
    height: 410px;
    padding: 20px;
  }

  @include onMobile {
    max-width: none;
    width: 100%;
    height: 100%;
  }

  &:hover {
    background: $exapmle-hovered-bckg;
  }

  &:hover > * {
    color: $example-hovered-text;
  }

  &__icon {
    background: $poster-bckg;
    border: 1px solid $poster-border;
    width: 100px;
    height: 100px;
    border-radius: 90px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title {
    padding-top: 22px;
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: $text-color-one;
  }

  &__txt {
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
    color: $text-color-two;
    padding-top: 8px;
  }
}

.process {
  position: relative;
  margin: 24vw 0;

  @include onTablet {
    margin-bottom: 0;
  }

  &__title {
    font-style: normal;
    font-weight: 800;
    font-size: 48px;
    line-height: 63px;
    font-family: "Catamaran";
    padding-bottom: 85px;
    text-align: center;
    color: $text-color-one;

    @include onTablet {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__container {
    position: relative;
    max-width: 1064px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 45vw;
    max-height: 630px;

    @include onTablet {
      max-width: 764px;
    }

    @include onMobile {
      height: 100%;
      max-height: none;
    }
  }

  &__flow {
    position: absolute;
    right: 0px;
    top: -10vw;
    width: 32vw;
    z-index: -1;

    @include onMobile {
      width: 90vw;
      top: 2vw;
    }

    &-img {
      width: 100%;
    }
  }
}

.row {
  display: flex;
  justify-content: space-between;
  z-index: 2;

  @include onMobile {
    flex-direction: column;
    align-items: center;
    gap: 58px;

    &:first-child {
      margin-bottom: 58px;
    }
  }

  &__box {
    display: flex;
    max-width: 355px;
    animation-delay: 0.6s;
    animation-name: fadeInUp;

    &-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      background: $default-white;
      box-shadow: 7px 10px 30px rgb(110 153 163 / 17%);
      width: 90px;
      height: 90px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 20px;
    }
  }
}

.central {
  position: absolute;
  width: 100%;
  max-width: 692px;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);

  @include onTablet {
    max-width: 458px;

    opacity: 0.4;
  }

  @include onMobile {
    display: none;
  }

  &__icon {
    &-image {
      visibility: visible;
      animation-name: fadeInUp;
      width: 100%;
    }
  }
}

.text {
  max-width: 250px;

  &__one {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    font-family: "SF Pro Display";
    color: $text-color-two;
  }

  &__two {
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    padding-top: 4px;
    color: $text-color-one;
  }

  &__three {
    font-family: "SF Pro Display";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    padding-top: 10px;
    color: $text-color-two;
  }
}
